import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  DropzoneUserDetailsFragment,
  FederationEssentialsFragment,
  UserDetailedFragment,
} from 'app/api/operations';
import { User } from '../../../api/schema.d';

export type GhostFields = Pick<User, 'exitWeight' | 'name' | 'phone' | 'email'> &
  Pick<DropzoneUserDetailsFragment, 'role' | 'license'>;

interface IGhostEditState {
  original: UserDetailedFragment | null;
  open: boolean;
  federation: {
    value: FederationEssentialsFragment | null;
    error: null;
  };
  fields: {
    [K in keyof GhostFields]-?: {
      value: GhostFields[K] | null;
      error: string | null;
    };
  };
}

export const initialState: IGhostEditState = {
  original: null,
  open: false,
  federation: {
    value: null,
    error: null,
  },
  fields: {
    exitWeight: {
      value: null,
      error: null,
    },
    email: {
      value: '',
      error: null,
    },
    phone: {
      value: '',
      error: null,
    },
    name: {
      value: null,
      error: null,
    },
    role: {
      value: null,
      error: null,
    },
    license: {
      value: null,
      error: null,
    },
  },
};

export default createSlice({
  name: 'forms/ghost',
  initialState,
  reducers: {
    setFederation: (
      state: IGhostEditState,
      action: PayloadAction<FederationEssentialsFragment>
    ) => {
      state.federation.value = action.payload;
    },
    setField: <T extends keyof IGhostEditState['fields']>(
      state: IGhostEditState,
      action: PayloadAction<[T, IGhostEditState['fields'][T]['value']]>
    ) => {
      const [field, value] = action.payload;
      if (field in state.fields) {
        state.fields[field].value = value;
        state.fields[field].error = null;
      }
    },
    setFieldError: <T extends keyof IGhostEditState['fields']>(
      state: IGhostEditState,
      action: PayloadAction<[T, IGhostEditState['fields'][T]['error']]>
    ) => {
      const [field, error] = action.payload;

      if (field in state) {
        state.fields[field].error = error;
      } else {
        console.error('Cannot set error on ', field);
      }
    },

    setOpen: (
      state: IGhostEditState,
      action: PayloadAction<boolean | DropzoneUserDetailsFragment>
    ) => {
      if (typeof action.payload === 'boolean') {
        state.open = action.payload;
        state.original = null;
        state.fields = initialState.fields;
      } else {
        state.original = action.payload.user;
        state.open = true;
        state.federation.value = action.payload.license?.federation || null;
        state.fields.license.value = action.payload.license || null;

        if (typeof action.payload !== 'boolean') {
          Object.keys(action.payload.user).forEach((key) => {
            const payloadKey = key as keyof typeof action.payload;
            if (payloadKey in state.fields) {
              const typedKey = payloadKey as keyof typeof initialState['fields'];

              if (typedKey === 'license' || typedKey === 'role') {
                state.fields[typedKey].value = (action.payload as DropzoneUserDetailsFragment)[
                  typedKey
                ];
              } else {
                state.fields[typedKey].value = (action.payload as DropzoneUserDetailsFragment).user[
                  typedKey
                ];
              }
            }
          });
        }
      }
    },

    reset: (state: IGhostEditState) => {
      state.fields = initialState.fields;
      state.original = null;
    },
  },
});
